body {
  margin: 0;
  padding: 0;
  background-color: rgb(23, 23, 23);
  color: LightGrey;
  font-family: monospace;
}

table {
    margin: 1rem;
    border: 1px solid black;
    box-shadow: 5px 5px 10px rgb(31, 31, 31);
}

th, td {
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;
}

tr:nth-child(even) {
    background-color: rgb(31, 31, 31);
}

tr:nth-child(odd) {
    background-color: rgb(54, 54, 54);
}

tbody > tr:hover {
    background-color: rgb(156, 86, 33);
}

input {
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid black;
    background-color: rgb(23, 23, 23);
    color: LightGrey;
    text-align: center;
}

input:focus {
    outline: none;
}

.exchange {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 1rem;
}

.exchange-title {
    font-size: 24px;
    margin: 10px;
}

.button-close {
    transform: rotate(45deg);
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}

.order-book {
    display: inline-flex;
    flex-direction: row;
}

.dialog-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(54, 54, 54, 0.5);
}

.dialog {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    border: 1px solid black;
    border-radius: 10px;
    background-color: rgb(23, 23, 23);
    height: 5rem;
    z-index: 10;
}

.ticker {
    font-size: 18px;
    margin: 10px;
}

#button-add-exchange {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(31, 31, 31);
    box-shadow: 3px 3px 10px rgb(31, 31, 31);
    font-size: 40px;
    color: LightGrey;
    cursor: pointer;
    margin: 4rem;
    margin-top: 10rem;
}

#root {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
}
